@property --rotation {
    syntax: "<angle>";
    initial-value: 0deg;
    inherits: false;
}

@keyframes sunlight-rotate {
    from {
        --rotation: 0deg;
    }
    to {
        --rotation: 360deg;
    }
}

@keyframes move-clouds {
    0% {
        transform: translate(100%, 0);
    }
    50% {
        transform: translate(-100%, 0);
    }
    100% {
        transform: translate(100%, 0);
    }
}